import { Button } from "@/components/ui/button"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuGroup,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import { useRouter } from "next/navigation"
import {
  Avatar,
  AvatarBadge,
  AvatarFallback,
  AvatarGroup,
  AvatarGroupCount,
  AvatarImage,
} from "@/components/ui/avatar"
import { LogOut } from "lucide-react"
import NavItems from "./Navitems"

const UserDropdown = () => {
    const router=useRouter ();

    const handleSignout=async()=>{
        router.push('/sign-in')
    }

    const user={
        name:"John Doe",
        email:"WUyEw@example.com"
    }
  return (
   <DropdownMenu>

  <DropdownMenuTrigger asChild>

    <Button variant="ghost" className="flex items-center gap-3 text-gray-4 hover:text-yellow-100">

<div className="hidden md-flex flex-col items-start">
    <span className='text-base font-medium text-gray-400'>{user.name}</span>
</div>
    </Button>

  </DropdownMenuTrigger>

  <DropdownMenuContent className="text-gray-400">

    <DropdownMenuLabel>

    <div className="flex relative items-center gap-3 py-2">
    <Avatar className="h-10 w-10">

  <AvatarImage src="https://github.com/shadcn.png" />

  <AvatarFallback className="bg-yellow-500 text-yellow-900 text-sm font-bold">
{user.name[0]}
  </AvatarFallback>

    </Avatar>

    <div className="flex flex-col md-flex flex-col items-start">
    <span className='text-base font-medium text-gray-400'>{user.name}</span>
    <span className='text-sm text-gray-400'>{user.email}</span>
</div>

    </div>
    </DropdownMenuLabel>

    <DropdownMenuSeparator className="text-gray-400"/> 
    
    <DropdownMenuItem onClick={handleSignout} className="text-gray-100 text-md font-medium focus:bg-transparent focus:text-yellow-500 transition-colors cursor-pointer">
        <LogOut className="mr-2 h-4 w-4 hidden sm:block" />
        LogOut
    </DropdownMenuItem>

    <DropdownMenuSeparator className="hidden sm:block bg-gray-600" />

    <nav className="sm:hidden">
        <NavItems/>
    </nav>


  </DropdownMenuContent>

</DropdownMenu>
  )
}

export default UserDropdown